import React from 'react';
import style from "./Sort.scss"
import SortList from "./SortList"
export default function Sort(props){
  const {reqs, setSort} = props;
  const handleClearClick = ()=>{
    setSort([]);
  }
  if(reqs.length < 1) return null
  return(
    <div className={"sort-list-container"}>
      <ul>
        <SortList list={reqs} deleteSort={props.deleteSort}/>
      </ul>
    <h1 onClick={handleClearClick} className={"clear-btn"}>Clear</h1>
    </div>
  )
};
