import React from 'react'
//Style
import style from "./JobListing.scss"
//Components
import CompImg from "./CompImg/CompImg"
import ProInfo from "./ProInfo/ProInfo"
import SubInfo from "./SubInfo/SubInfo"
import Requirements from "./Requirements/Requirements"
//Data
import data from "../data.json"
const concat = (...arr) => [].concat(...arr);
export default function JobListing(props) {
    const jobListingMap = data.map(dd =>{
        const concatReqs = concat(dd.role, dd.level, dd.languages, dd.tools)
        const contains =  props.sortBy.every(s =>{
            return concatReqs.indexOf(s) !== -1
        })
        if(props.sortBy.length > 0 && !contains) return null
          return(
               <div key={dd.id} className={"company-listing" + (dd.featured ? " is-featured" : "")}>
                 <CompImg imgPath={dd.logo}/>
                 <div className="company-info">
                   <ProInfo companyName={dd.company} isNew={dd.new} isFeatured={dd.featured}/>
                   <h1 className={"company-info__position"}>{dd.position}</h1>
                   <SubInfo postedAt={dd.postedAt} contract={dd.contract} location={dd.location}/>
                 </div>
                 <Requirements reqs={concatReqs} setSort={props.setSort}/>
               </div>
          )
        })
        return jobListingMap;
}
