import React from 'react'

export default function ProInfo(props) {
    const {companyName, isNew, isFeatured} = props;
    return (
        <div className={"pro-info"}>
            <ul>
                <li>{companyName}</li>
                {isNew && <li className={"pro-info__new"}>New!</li>}
                {isFeatured && <li className={"pro-info__featured"}>Featured</li>}
            </ul>
        </div>
    )
}
