import React from 'react'

export default function Requirements(props) {
    const handleClick = (e) => {
        const target = e.target;
        if(target.nodeName !== "LI") return;
        props.setSort(target.textContent)
    }
    return(
        <ul onClick={handleClick} className={"requirements"}>
             {props.reqs.map((s,i) => {
                if(typeof s === "undefined") return null
        return <li key={i} className={"requirements__item"}>{s}</li>
            })}
        </ul>
    )
}
