import React from 'react';
import iconRemove from "../../images/icon-remove.svg"
export default function SortList(props){
  const {list, deleteSort} = props
  const handleClick = (e)=>{
    const target = e.target;
    if(target.nodeName === "DIV"){
      deleteSort(target.previousSibling.textContent)
    }
    else{
      deleteSort(target.parentNode.previousSibling.textContent)
    }
  }
  return list.map(s => {
    return (<li key={s} className={"sort-list__item"}>
              <div className={"sort-list__item-name"}>{s}</div>
              <div onClick={handleClick} className={"sort-list__item-del"}><img src={iconRemove} alt="remove icon"></img></div>
            </li>)
    })
}
