import React, {useState} from 'react'
import style from "./App.scss"
//Components
import JobListing from "./components/JobListing"
import Sort from "./components/Sort/Sort"
export default function App() {
  const [sortArr, setSortArr] = useState([]);
  const handleClick=(name)=>{
    if(sortArr.indexOf(name) !==-1) return;
      setSortArr([...sortArr, name])
  }
  const deleteSortItem = (item)=>{
    setSortArr(sortArr.filter(s => s !== item));
  }
  return (
    <>
        <div className="hero">
        </div>
        <Sort reqs={sortArr} setSort={setSortArr} deleteSort={deleteSortItem}/>
      <main>
        <JobListing setSort={handleClick} sortBy={sortArr}/>
      </main>
    </>
    )
}
